#modalRoot {
    height: 100vh;
    width: 100vw;
}

.inner-box {
    background: #efefec;
    height: auto;
    border: 1px solid #d8d8d8;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.content {
    min-height: 100px;
    min-width: 400px;
    width: 25vw;
    padding: 30px;
    position: relative;
}

.close-btn {
    position: absolute;
    right: 10px;
    top: 10px;
    color: #aaa;
    padding:0;
}
